export default {
	data() {
		return {
			show: false
		}
	},
	methods: {
		onSelAddr() { //选择地区
			this.show = true;
		},
		onAddrConfirm(val) { //确定选择
			this.show = false;
			this.addrInfo = val[0].name + val[1].name + val[2].name
		},
		onAddrCancel() { //取消选择
			this.show = false;
		}
		// showPopup() {
